import {common} from "../utils/api/url";
import DialogNormal from "../components/utils/dialog/DialogNormal";
import TimeoutButton from "../components/utils/button/TimeoutButton";
import {MessageSuccess, MessageError, getmd5, MessageWarning} from "../utils/utils";
import loginPhoneValidater from "../utils/validater/loginPhoneValidater";
import {Toast} from "vant";

export default {
  layout: 'defaultFullScreen',
  mixins: [loginPhoneValidater],
  components: {DialogNormal,TimeoutButton},
  data(){
    return {
      userType: '',
      userSubType: '2',
      userAccountType : '0',
      pageType: '',
      dialogLoading: false,
      modalVisible: false,
      showContent: false,
      testVisible: true,
      updatePhoneMms: common.send_active_account,
      form: {
        username: '',
        password: '',
        campusNo: ''
      },
      formAuth: {
        phone: '',
        code: '',
        userId: ''
      },
      fullHeight: {
        height: ''
      }
    }
  },
  mounted() {
    // 监听窗口大小
    window.onresize = () => {
      this.hh();
    };

    this.showContent = true;
  },
  beforeCreate: function(){
    this.showContent = false;
  },
  created() {
    if (process.browser) {
      let pageType = localStorage.getItem("pageType");
      this.userSubType = "2";
      if (pageType == 'server'){
        this.userSubType = "1";
      }
    }
    this.hh();
  },
  methods: {
    hh(){
      if (process.browser) {
        this.fullHeight.height = window.innerHeight + 20 + 'px';
      }
    },
    changeUserType(type){
      this.form.username = "";
      this.form.password = "";
      this.form.campusNo = "";
      this.userSubType = "1";
      this.userType = type;
    },
    changeSubType(type){
      this.form.username = "";
      this.form.password = "";
      this.form.campusNo = "";
      this.userSubType = type;
      if (type == 1){
        this.userAccountType = 0;
      }else if (type == 2){
        this.userAccountType = 3;
      }
    },
    login(){
      let userType = "";
      if (this.form.username == "" || this.form.password == ""){
        Toast(this.$t("请输入正确的信息！"));
        return;
      }
      let params = {
        clientType: 3,
        accountType: 0,
        account: this.form.username,
        password: getmd5(this.form.password),
      };
      if (this.userSubType == 1){
        //params['campusNo'] = this.form.campusNo;
        params['accountType'] = 0;
      }else if (this.userSubType == 2){
        params['accountType'] = 3;
      }
      // if (this.userType == 1){
      //   userType = 5;
      // }if (this.userType == 2){
      //   userType = 4;
      // }
      // params['userType'] = userType;
      params = this.$qs.stringify(params);
      this.dialogLoading = true;
      this.$axios.post(common.login_url, params, {loading: false}).then(res => {
        if (res.data.code == 200){
          let pageType = localStorage.getItem("pageType");
          if (pageType == 'server'){
            this.$router.push("/app/appIndex");
          }else {
            this.$router.push("/newStudent/studentIndex");
          }
          localStorage.removeItem("menuTabList");
          Toast(res.data.desc);
        }else if (res.data.code == 2032){
          this.formAuth.userId = res.data.data.userInfo.userId;
          this.modalVisible = true;
        }else {
          Toast(res.data.desc);
        }
        this.dialogLoading = false;
      });
    },
    closeDialog(event){
      let _self = this;
      this.formAuth = {
        phone: '',
        code: '',
        userId: ''
      };
      if (this.$refs['formAuth']){
        this.$refs['formAuth'].resetFields();
      }
    },
    authBefore(){
      let phoneReg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
      if (this.formAuth.phone == ""){
        Toast(this.$t("请输入正确的手机号"));
        return false;
      }else if (!phoneReg.test(this.formAuth.phone)){
        return false;
      }
      return true;
    },
    okDialog(event){
      let url = "";
      this.$refs['formAuth'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            phone: this.formAuth.phone,
            captcha: this.formAuth.code
          };

          params = this.$qs.stringify(params);
          this.$axios.post(common.set_active_account, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.modalVisible = false;
              this.userType = '';
              this.userSubType = 1;
              this.form.username = "";
              this.form.password = "";
              this.form.campusNo = "";
              Toast(res.data.desc +","+ this.$t("请使用激活的手机号进行登录！"));
            }else {
              Toast(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    cancelDialog(){
      this.modalVisible = false;
    }
  }
}
